.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.label {
  font-size: 16px;
  margin-bottom: 8px;
  width: 300px;
  display: flex;
  flex-direction: column;
}


.input {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
  margin-bottom: 16px;
  width: 100%;
}
.inputError {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ff0000;
  font-size: 16px;
  margin-bottom: 16px;
  width: 100%;
}
.input:focus {
  outline: none;
  border-color: #0077ff;
}
.button {
  background-color: #0077ff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 250ms ease-in-out;
}

.button:hover {
  background-color: #005ae6;
}
.errorMessage {
  font-size: 14px;
  color: #ff0000;
}
.title{
  text-align: center;
}